import { Col, Form, Input, Layout, Row, Table, Tag } from "antd";
import "antd/dist/antd.css";
import icons from "currency-icons";
import * as JsSearch from "js-search";
import React, { Component } from "react";
import conferences from "../../static/conferences.yaml";
import moment from "moment";
import { randomColor } from "./randomColor.js";

const { Header, Content } = Layout;

const colors = Object.fromEntries(
  new Map(
    [...new Set(conferences.conferences.flatMap((item) => item.topics))].map(
      (item) => [item, randomColor(item)]
    )
  )
);

const topics = [
  ...new Set(conferences.conferences.flatMap((item) => item.topics)),
];
const formats = [
  ...new Set(conferences.conferences.map((item) => item.format)),
];

const languages = [
  ...new Set(conferences.conferences.map((item) => item.language)),
];

const columns = [
  {
    title: "Name",
    dataIndex: "name",
    key: "name",
    render: (text, record) => (
      <a href={record.url} target="_blank" rel="noreferrer">
        {text}
      </a>
    ),
    sorter: {
      compare: (a, b) => new Intl.Collator().compare(a.name, b.name),
      sortDirections: ["ascend", "descend"],
    },
  },
  {
    title: "Start date",
    dataIndex: "date_start",
    key: "date_start",
    render: (text) => moment(text).format("yyyy-MM-DD"),
    sorter: {
      compare: (a, b) => {
        const x = moment(a.date_start);
        const y = moment(b.date_start);
        return (x < y) - (y < x);
      },
      sortDirections: ["ascend", "descend"],
    },
  },
  {
    title: "End date",
    dataIndex: "date_end",
    key: "date_end",
    render: (text) => moment(text).format("yyyy-MM-DD"),
    sorter: {
      compare: (a, b) => {
        const x = moment(a.date_start);
        const y = moment(b.date_start);
        return (x < y) - (y < x);
      },
      sortDirections: ["ascend", "descend"],
    },
  },
  {
    title: "Price",
    dataIndex: "price",
    key: "price",
    render: (text, record) => `${icons[record.currency].symbol}${record.price}`,
  },
  {
    title: "Format",
    dataIndex: "format",
    key: "format",
    filters: formats.map((it) => {
      return { text: it, value: it };
    }),
    onFilter: (value, record) => {
      return record.format === value;
    },
    sorter: {
      compare: (a, b) => new Intl.Collator().compare(a.format, b.format),
      sortDirections: ["ascend", "descend"],
    },
  },
  {
    title: "CFP deadline",
    dataIndex: "cfp_deadline",
    key: "cfp_deadline",
    render: (text) => (text ? moment(text).format("yyyy-MM-DD") : "n/a"),
  },
  {
    title: "language",
    dataIndex: "language",
    key: "language",
    onFilter: (value, record) => {
      return record.format === value;
    },
    sorter: {
      compare: (a, b) => new Intl.Collator().compare(a.format, b.format),
      sortDirections: ["ascend", "descend"],
    },
    render: (text) => text.toUpperCase(),
    filters: languages.map((it) => {
      return { text: it, value: it };
    }),
  },
  {
    title: "Topics",
    dataIndex: "topics",
    key: "topics",
    render: (tags) => (
      <>
        {tags.map((tag) => {
          let color = colors[tag];
          return (
            <Tag color={color} key={tag}>
              {tag.toUpperCase()}
            </Tag>
          );
        })}
      </>
    ),
    filters: topics.map((it) => {
      return { text: it, value: it };
    }),
    onFilter: (value, record) => {
      return record.topics.indexOf(value) !== -1;
    },
  },
];

class Search extends Component {
  state = {
    confList: conferences.conferences,
    search: [],
    searchResults: [],
    isLoading: true,
    isError: false,
    searchQuery: "",
  };

  async componentDidMount() {
    this.rebuildIndex();
  }

  /**
   * rebuilds the overall index based on the options
   */
  rebuildIndex = () => {
    const { confList } = this.state;
    const dataToSearch = new JsSearch.Search("url");
    dataToSearch.indexStrategy = new JsSearch.PrefixIndexStrategy();
    dataToSearch.sanitizer = new JsSearch.LowerCaseSanitizer();

    dataToSearch.searchIndex = new JsSearch.TfIdfSearchIndex("url");

    // searching by these fields only:
    dataToSearch.addIndex("name");
    dataToSearch.addIndex("date_start");
    dataToSearch.addIndex("date_end");
    dataToSearch.addIndex("topics");

    dataToSearch.addDocuments(confList); // adds the data to be searched
    this.setState({ search: dataToSearch, isLoading: false });
  };

  /**
   * handles the input change and perform a search with js-search
   * in which the results will be added to the state
   */
  searchData = (e) => {
    const { search } = this.state;
    const queryResult = search.search(e.target.value);
    this.setState({ searchQuery: e.target.value, searchResults: queryResult });
  };
  handleSubmit = (e) => {
    e.preventDefault();
  };

  render() {
    const { confList, searchResults, searchQuery } = this.state;
    const queryResults = searchQuery === "" ? confList : searchResults;
    return (
      <Layout className="layout">
        <Header>
          <h1>Hi!</h1>
        </Header>
        <Content style={{ padding: "0 50px" }}>
          <Row>
            <Col span={24}>
              <Form onSubmit={this.handleSubmit}>
                <Input
                  id="Search:"
                  value={searchQuery}
                  onChange={this.searchData}
                  placeholder="Search"
                  style={{ margin: "0 auto", width: "400px" }}
                />
              </Form>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table columns={columns} dataSource={queryResults} />
            </Col>
          </Row>
        </Content>
      </Layout>
    );
  }
}
export default Search;
